<template>
  <div>
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <resize-table>
        <table class="table">
          <thead>
            <tr>
              <th width="20%">角色名称</th>
              <th width="60%">备注</th>
              <th width="20%">操作</th>
            </tr>
          </thead>
          <tbody>
          <tr v-for="item in list">
            <td>{{item.name}}</td>
            <td>{{item.remark}}</td>
            <td>
              <span @click="jump(item)" class="oper">查看权限</span>
            </td>
          </tr></tbody>
        </table>
      </resize-table>
    </main-part>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "角色管理", path: ""}],
        list: []
      }
    },
    methods: {
      async getDetail() {
        this.list = await this.$ajax({
          url: '/role/queryRoleList.do',
          el: '.table'
        })
      },
      // 跳转
      jump(item) {
        this.$router.push({
          path: `/school/role/${item.id}`,
          query: {
            name: item.name
          }
        })
      }
    },
    mounted() {
      this.getDetail()
    }
  }
</script>
